.aut-block {
  background: #FFFFFF;
  border-radius: 44px;
  padding: 25px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 400px;

  &.logo {
    color: #000;
  }
}

.aut-block__text {
  margin: 30px 0;
  color: #000;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
}

.aut-inp__text {
  color: #000;
  margin-bottom: 5px;
  text-align: center;
}

.aut-block__inp {
  display: block;
  width: 100%;
  outline: none;
  height: 44px;
  background: #FFFFFF;
  border: 1px solid #C6C6C6;
  border-radius: 10px;
  margin-bottom: 25px;
  margin-top: 10px;
  margin-right: auto;
  margin-left: auto;
  font-size: 20px;
  padding: 10px;
  box-sizing: border-box;
}

.aut-btn {
  background: #FF6E30;
  border-radius: 10px;
  padding: 10px 30px;
  border: none;
  color: #FFF;
  font-size: 14px;
  line-height: 16px;
  cursor: pointer;

}